import Cookies from "js-cookie";

// 浏览器本地保存操作

function cookieGet(key = '') {
    /** 获取本地cookie保存值 */
    return new Promise((resolve, reject) => {
        if (key.length < 1) {
            reject('Error key...');
        }
        resolve(Cookies.get(key));
    });
}

function cookieSet(key, val, expiresDay=7) {
    /** 保存本地cookie */
    return Cookies.set(key, val, {expires: expiresDay});
}

function cookieRemove(key) {
    /** 删除本地cookie */
    return Cookies.remove(key);
}

export {
    cookieGet,
    cookieSet,
    cookieRemove,
};
