import Cookies from "js-cookie";
import { storageKeys } from '../custom_configs';

const companyTokenKey = storageKeys.loginTokenKey;

function getToken() {
    /** 获取本地token */
    return Cookies.get(companyTokenKey);
}

function setToken(token) {
    /** 保存本地token */
    return Cookies.set(companyTokenKey, token, { expires: 180 });
}

function removeToken() {
    /** 保存本地token */
    return Cookies.remove(companyTokenKey);
}

export {
    getToken,
    setToken,
    removeToken,
}