import React from 'react';
import { AxiosRequest } from '../../../apis/request';

const StsTemporaryKeyRequestPost = ({requestCount, reqObject, responseDataHandle}) => {
  // 获取上传文件的临时密钥 和 cos_name
  const api = 'imageshandle/v1/images';
  const method = 'post';
  const reqParams = {};

  return (
    <AxiosRequest
      responseDataHandle={responseDataHandle} api={api} method={method}
      reqParams={reqParams} reqObject={reqObject} requestCount={requestCount}
    ></AxiosRequest>
  )
};

StsTemporaryKeyRequestPost.defaultProps = {
  requestCount: 0,
  reqObject: {},
  responseDataHandle: (respObj) => { console.log(respObj); },
};

export { StsTemporaryKeyRequestPost };
