import React, { useEffect, useState } from 'react';

const SecondCountDownComponent = ({maxSecondNumber, tips, currentSecondHandle}) => {
    // 读秒倒计时组件
    const [currentSecond, setCurrentSecond] = useState(maxSecondNumber);

    useEffect(() => {
        if (currentSecond > 0) {
            // 停止倒计时
            const countdownInterval = setInterval(() => {
                const updatedSecond = currentSecond - 1;
                // console.log(updatedSecond);
                setCurrentSecond(updatedSecond);
                currentSecondHandle(updatedSecond);
            }, 1000);
            return () => {
                clearInterval(countdownInterval);
            }
        }
    }, [currentSecond]);

    return (
        <div style={{ width: 'auto', height: '100%', }}>
            <p style={{ fontSize: 8, display: 'inline', }}>{currentSecond} {tips}</p>
        </div>
    )
}

SecondCountDownComponent.defaultProps = {
    maxSecondNumber: 60,
    tips: '秒',
    currentSecondHandle: updatedSecond => console.log(updatedSecond),
}

export { SecondCountDownComponent, };
