import React from 'react';
import { Link } from 'gatsby';

const MenuTextButton = ({ children, to }) => {
    // 文字按钮
    return (
        <div className="m-2">
            <Link to={to} className="font-medium font-mono text-sm subpixel-antialiased text-gray-50 cursor-pointer">{children}</Link>
        </div>
    );
};

MenuTextButton.defaultProps = {
    children: '',
    onClick: (clickArgs) => console.log(clickArgs),
    clickArgs: {},
};

export {
    MenuTextButton,
};
