import React, { useState } from 'react';
import { Button, Space, Form, Input, message, } from 'antd';
import { AxiosRequest, } from '../../apis/request';
import { labelsApi, } from '../../apis/server_apis';
import { sendVerifyCode, } from '../sendVerifyCode/sendVerifyCode';
import { SecondCountDownComponent, } from '../base_components/countdown/secondCountDown';
import { cookieSet, } from '../../funcs/localSaveHandle/cookie_handle';
import { storageKeys, siteStyles, } from '../../custom_configs';

const layout = siteStyles.formLayoutStyle;
const tailLayout = siteStyles.formTailLayoutStyle;

const VerifyCodeForm = ({ setValidVerifyCodeState, formValuesChange, }) => {
    // 发送验证码form
    const [verifyCodeValidApi, setVerifyCodeValidApi] = useState(labelsApi.verifyCodeValid);
    const [verifyCodeValidObj, setVerifyCodeValidObj] = useState({ email: '', verification_code: '', });
    const [verifyCodeValidRequestCount, setVerifyCodeValidRequestCount] = useState(0);

    const [isloading, setIsLoading] = useState(0);

    const emailChange = (changedValues, allValues) => {
        setVerifyCodeValidObj({
            email: allValues.email,
            verification_code: allValues.verificationCode,
        });
        formValuesChange(changedValues, allValues);
    }

    const countdownHandle = (currentSecond) => {
        // console.log(currentSecond);
        if (currentSecond === 0) {
            // 倒计时结束显示发送按钮
            setIsLoading(0);
        }
    };

    const sendValidCode = () => {
        // 请求发送验证码
        setIsLoading(2);        // 显示倒计时
        sendVerifyCode({ email: verifyCodeValidObj.email, }).then(resp => {
            console.log(resp);
        }).catch(err => {
            console.log(err);
        });
    };

    const onFinish = (values) => {
        // 提交表单 验证验证码是否正确
        setVerifyCodeValidObj({
            email: values.email,
            verification_code: values.verificationCode,
        });
        setVerifyCodeValidRequestCount(verifyCodeValidRequestCount + 1);
    }

    const validVerifyCodeResponseDataHandle = (resp) => {
        // 判断验证码是否正确 请求结果
        console.log(resp);
        if (resp.code && resp.code === 1000) {
            setValidVerifyCodeState(true);
        } else {
            message.error('验证码或邮箱错误...');
        }
    }

    return (
        <>
            <Form
                {...layout}
                name="找回密码"
                initialValues={{ remember: true, }}
                onFinish={onFinish}
                onValuesChange={emailChange}
            >
                <Form.Item
                    name="email"
                    label="邮箱"
                    rules={[
                        { type: 'email', message: 'The input is not valid E-mail!', },
                        { required: true, message: 'Please input your E-mail!', },
                    ]}
                >
                    <Space>
                        <Input />
                        {
                            isloading === 2
                            ? <SecondCountDownComponent maxSecondNumber={120} tips="秒后重发" currentSecondHandle={countdownHandle}></SecondCountDownComponent>
                            : <Button type="link" onClick={sendValidCode}>发送验证码</Button>
                        }
                    </Space>
                </Form.Item>
                <Form.Item
                    name="verificationCode"
                    label="验证码"
                    rules={[{ required: true, message: 'Please input Verification Code!', },]}
                ><Input /></Form.Item>

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit">下一步</Button>
                </Form.Item>
            </Form>
            {/* 查看验证码是否正确请求 */}
            <AxiosRequest
                responseDataHandle={validVerifyCodeResponseDataHandle} api={verifyCodeValidApi.api} method={verifyCodeValidApi.method}
                reqObject={verifyCodeValidObj} requestCount={verifyCodeValidRequestCount}
            ></AxiosRequest>
        </>
    )
};

const FindPasswordForm = ({ emailAndVerifyCodeObj, setValidVerifyCodeState, findPasswordResponseHandle, formValuesChange, }) => {
    // 修改密码表格
    const [requestObj, setRequestObj] = useState({});
    const [requestCount, setRequestCount] = useState(0);
    const [isloading, setIsLoading] = useState(0);

    const [findPasswordApi, setFindPasswordApi] = useState(labelsApi.findPassword)

    const onFinish = (values) => {
        // 提交修改密码
        setIsLoading(1);
        console.log(emailAndVerifyCodeObj);
        const findPasswordReqObj = { ...emailAndVerifyCodeObj, password: values.password, };
        setRequestObj(findPasswordReqObj);
        setRequestCount(requestCount + 1);
    };

    const responseDataHandle = (resp) => {
        // 更新密码请求结果
        if (resp.code && resp.code === 1000) {
            // 更新密码成功
            findPasswordResponseHandle(resp);
        } else {
            message.error('修改密码失败...');
        }
    }

    return (
        <>
            <Form
                {...layout}
                name="找回密码"
                onFinish={onFinish}
                onValuesChange={formValuesChange}
            >
                <Form.Item
                    label="新密码"
                    name="password"
                    rules={[{ required: true, message: 'Please input your password!', min: 6, }]}
                ><Input.Password /></Form.Item>

                <Form.Item {...tailLayout}>
                    <Space>
                        <Button onClick={() => setValidVerifyCodeState(false)}>上一步</Button>
                        <Button type="primary" htmlType="submit" loading={isloading === 1}>修改密码</Button>
                    </Space>
                </Form.Item>
            </Form>
            {/* 更改密码请求 */}
            <AxiosRequest
                responseDataHandle={responseDataHandle} api={findPasswordApi.api} method={findPasswordApi.method}
                reqObject={requestObj} requestCount={requestCount}
            ></AxiosRequest>
        </>
    )
}

const CompanyFindPasswordForm = ({ setShowLoginOrRegist, responseHandle, }) => {

    const [emailAndVerifyCodeObj, setEmailAndVerifyCodeObj] = useState({ email: '', verification_code: '', });

    const [loginApi, setLoginApi] = useState(labelsApi.login);
    const [validVerifyCodeState, setValidVerifyCodeState] = useState(false);  // 验证码是否有效

    const [isloading, setIsLoading] = useState(0);

    const [loginRequestObj, setLoginRequestObj] = useState({ email: '', password: '', });
    const [loginRequestCount, setLoginRequestCount] = useState(0);

    const loginHandle = () => {
        // 登陆操作
        console.log(emailAndVerifyCodeObj);
        const loginRequestObj = {
            email: emailAndVerifyCodeObj.email,
            password: emailAndVerifyCodeObj.password,
        };
        // console.log(emailAndVerifyCodeObj);
        setLoginRequestObj(loginRequestObj);
        setLoginRequestCount(loginRequestCount + 1);
    };

    const findPasswordResponseDataHandle = (resp) => {
        console.log(resp);
        setIsLoading(0);
        if (resp.code && resp.code === 1000) {
            message.success('密码修改成功!');
            // 找回密码成功后直接登陆
            loginHandle();
        }
    };

    const loginResponseDataHandle = (loginResp) => {
        // 找回密码成功后直接登陆返回结果
        if (loginResp.code && loginResp.code === 1000) {
            const currentToken = loginResp.token ? loginResp.token : '';
            cookieSet(storageKeys.loginTokenKey, currentToken, 180);
            gotoLogin();
            responseHandle(loginResp);
        }
    }

    const gotoLogin = () => {
        // 显示登陆页面
        setShowLoginOrRegist(1);
    }

    const verifyCodeValueChange = (changedValues, allValues) => {
        // 更新email值
        const copyObj = { ...emailAndVerifyCodeObj };
        if (allValues.email) copyObj.email = allValues.email;
        if (allValues.verificationCode) copyObj.verification_code = allValues.verificationCode;
        if (allValues.password) copyObj.password = allValues.password;

        setEmailAndVerifyCodeObj(copyObj);
    }

    return (
        <>
            {
                validVerifyCodeState ? 
                <FindPasswordForm 
                    emailAndVerifyCodeObj={emailAndVerifyCodeObj}
                    setValidVerifyCodeState={setValidVerifyCodeState}
                    findPasswordResponseHandle={findPasswordResponseDataHandle}
                    formValuesChange={verifyCodeValueChange}
                ></FindPasswordForm> :
                <VerifyCodeForm
                    setValidVerifyCodeState={setValidVerifyCodeState}
                    formValuesChange={verifyCodeValueChange}
                ></VerifyCodeForm>
            }
            <Button type="link" onClick={gotoLogin} loading={isloading === 1}>去登陆</Button>
            {/* 登陆请求 */}
            <AxiosRequest
                responseDataHandle={loginResponseDataHandle} api={loginApi.api} method={loginApi.method}
                reqObject={loginRequestObj} requestCount={loginRequestCount}
            ></AxiosRequest>
        </>
    )
};

CompanyFindPasswordForm.defaultProps = {
    setShowLoginOrRegist: (showNumber) => console.log(showNumber),
    responseHandle: (resp) => console.log(resp),
};

export { CompanyFindPasswordForm, };
