import React from 'react';
import { Modal, Select, Form, Input, InputNumber } from 'antd';
import { StsUploadFile } from '../base_components/upload_image_file/sts_upload_file';

const ModalUploadImage = ({ showVisibleNum, visible, setVisible, uploadedResponseHandle }) => {
    // 上传图片对话框
    return (
        <Modal
            title="上传Logo"
            visible={showVisibleNum === visible}
            onOk={() => setVisible(0)}
            onCancel={() => setVisible(0)}
        >
            <StsUploadFile uploadedResponseHandle={uploadedResponseHandle}></StsUploadFile>
        </Modal>
    )
};

ModalUploadImage.defaultProps = {
    showVisibleNum: 1,
    visible: false,
    setVisible: (numberVal) => console.log(numberVal),
    uploadedResponseHandle: (resp) => console.log(resp),
};

export { ModalUploadImage };
