import React, { useEffect, useState, } from 'react';
import { message, } from 'antd';
import { AxiosRequest, } from '../../apis/request';
import { labelsApi, } from '../../apis/server_apis';
import { sendAxiosRequest, } from '../../apis/common_request';
import { isEmail, } from '../../funcs/verifyFuncs/verifyEmail';

const RequestSendVerifyCode = ({ mobileOrEmailObj, requestCount, }) => {
    // Request Server send verify code
    const [sendVerifyCodeApi, setSendVerifyCodeApi] = useState(labelsApi.verifyCode);
    const [requestObj, setRequestObj] = useState({});

    const makeRequestObj = () => {
        // 创建请求 json 对象
        const mobile = mobileOrEmailObj.mobile ? mobileOrEmailObj.mobile : null;
        const email = mobileOrEmailObj.email ? mobileOrEmailObj.email : null;
        let requestObj = {};
        if (mobile) {
            requestObj = { mobile: mobile, };
        } else if (email) {
            requestObj = { email: email, };
        }
        return requestObj;
    }

    useEffect(() => {
        // 更新请求对象
        setRequestObj(makeRequestObj());
    }, [mobileOrEmailObj]);

    const responseDataHandle = (resp) => {
        // 返回结果
        if (resp.code === 1000) {
            message.success('验证码已成功发送!');
        } else {
            message.error('验证码发送失败...');
        }
        console.log(resp);
    }

    return (
        <AxiosRequest
            responseDataHandle={responseDataHandle} api={sendVerifyCodeApi.api} method={sendVerifyCodeApi.method}
            reqObject={requestObj} requestCount={requestCount}
        ></AxiosRequest>
    )
};

RequestSendVerifyCode.defaultProps = {
    mobileOrEmailObj: {},
    requestCount: 0,
};

const sendVerifyCode = (mobileOrEmailObj) => {
    // 发送验证码函数
    return new Promise((resolve, reject) => {
        const sendVerifyCodeApiObj = labelsApi.verifyCode;

        const mobile = mobileOrEmailObj.mobile ? mobileOrEmailObj.mobile : null;
        const email = mobileOrEmailObj.email ? mobileOrEmailObj.email : null;
        // let requestObj = {};
        if (mobile) {
            // const requestObj = { mobile: mobile, };
            reject({ code: 5000, tips: 'Error: 未开通短信验证码', })
            message.error('未开通短信验证码...');
        } else if (email && isEmail(email)) {
            if (!isEmail(email)) {
                message.warn('请输入正确的邮箱...');
                reject({ code: 4002, tips: 'Error: 请输入正确的邮箱', })
            } else {
                sendAxiosRequest(sendVerifyCodeApiObj.api, sendVerifyCodeApiObj.method, {}, { email: email, }).then(resp => {
                    // 请求Email验证码
                    console.log(resp)
                    resolve(resp);
                    message.success('发送邮箱验证码成功!');
                }).catch(err => {
                    console.error(err);
                    reject(err);
                    message.error('发送证码失败...');
                });
            }
        } else {
            // 不发送
            reject({ code: 4001, tips: 'Error: None mobile or email', });
        }
    });
}

export { RequestSendVerifyCode, sendVerifyCode };
