import React, { useEffect, useState, } from 'react';
import { Drawer } from 'antd';
import { CompanyLoginForm, } from './login_form';
import { CompanyRegistForm, } from './regist_form';
import { CompanyFindPasswordForm, } from './find_password_form';
import { getCompanyData } from '../CompanyInfo/get_company_info';

const CompanyLoginDrawer = ({visible, setVisible, companyInfo, setCompanyInfo}) => {

    const [showLoginOrRegist, setShowLoginOrRegist] = useState(1);
    const [drawerTitle, setDrawerTitle] = useState('用户登陆');

    useEffect(() => {
        switch (showLoginOrRegist) {
            case 1:
                setDrawerTitle('用户登陆');
                break;
            case 2:
                setDrawerTitle('请注册');
                break;
            case 3:
                setDrawerTitle('找回密码');
                break;
            default:
                break;
        }
    }, [showLoginOrRegist]);

    const loginRegistFindPasswordResponseHandle = (resp) => {
        // 登陆、注册、找回密码 response 操作
        console.log(resp);
        if (resp.code && resp.code === 1000) {
            // 成功后关闭 drawer
            setVisible(0);
            getCompanyData().then(companyInfoObj => {
                console.log(companyInfoObj);
                setCompanyInfo(companyInfoObj);
            }).catch(err => {
                console.log(err);
                setCompanyInfo({});
            })
            
        }
    }

    return (
        <>
            <Drawer
                title={drawerTitle}
                placement="left"
                closable={false}
                onClose={() => setVisible(0)}
                visible={visible}
                key="left"
                width={380}
            >
                {showLoginOrRegist === 1
                    ? <CompanyLoginForm setShowLoginOrRegist={setShowLoginOrRegist} responseHandle={loginRegistFindPasswordResponseHandle}></CompanyLoginForm>
                    : null
                }
                {showLoginOrRegist === 2
                    ? <CompanyRegistForm setShowLoginOrRegist={setShowLoginOrRegist} responseHandle={loginRegistFindPasswordResponseHandle}></CompanyRegistForm>
                    : null
                }
                {showLoginOrRegist === 3
                    ? <CompanyFindPasswordForm setShowLoginOrRegist={setShowLoginOrRegist} responseHandle={loginRegistFindPasswordResponseHandle}></CompanyFindPasswordForm>
                    : null
                }
            </Drawer>
        </>
    )
}

CompanyLoginDrawer.defaultProps = {
    visible: false,
    setVisible: () => console.log('~~~~~~~~ ! Visible ~~~~~~~~~')
}

export { CompanyLoginDrawer };
