import React, { useState } from 'react';
import { Button, Form, Input, Checkbox, message } from 'antd';
import { AxiosRequest } from '../../apis/request';
import { labelsApi } from '../../apis/server_apis';
import { cookieSet, } from '../../funcs/localSaveHandle/cookie_handle';
import { storageKeys, } from '../../custom_configs';

const CompanyLoginForm = ({ setShowLoginOrRegist, responseHandle }) => {

    const [requestObj, setRequestObj] = useState({ email: '', password: '', });
    const [requestCount, setRequestCount] = useState(0);

    const [loginApi, setLoginApi] = useState(labelsApi.login);

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    };

    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    };

    const onFinish = (values) => {
        // 发送请求 当前只实现 email 登陆
        console.log(values);
        const currentRequestObj = {
            email: values.email,
            password: values.password,
        };
        setRequestObj(currentRequestObj);
        setRequestCount(requestCount + 1);
    };

    const responseDataHandle = (resp) => {
        console.log(resp);
        if (resp.code && resp.code === 1000) {
            // 登陆成功
            const currentToken = resp.token ? resp.token : '';
            cookieSet(storageKeys.loginTokenKey, currentToken, 180);
            message.success('登陆成功!');
            responseHandle(resp);
        }
    };

    const gotoRegist = () => {
        // 显示注册页面
        setShowLoginOrRegist(2);
    }

    return (
        <>
            <Form
                {...layout}
                name="登陆"
                initialValues={{ remember: true, }}
                onFinish={onFinish}
            >
                <Form.Item
                    name="email"
                    label="E-mail"
                    rules={[
                        { type: 'email', message: 'The input is not valid E-mail!', },
                        { required: true, message: 'Please input your E-mail!', },
                    ]}
                ><Input /></Form.Item>

                <Form.Item
                    label="Password"
                    name="password"
                    rules={[{ required: true, message: 'Please input your password!' }]}
                ><Input.Password /></Form.Item>

                <Form.Item {...tailLayout} name="remember" valuePropName="checked">
                    <Checkbox>Remember me</Checkbox>
                </Form.Item>

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit">登陆</Button>
                </Form.Item>
                <Form.Item>
                    <Button type="link" onClick={gotoRegist}>没有账号去注册</Button>
                </Form.Item>
                <Form.Item>
                    <Button type="link" onClick={() => setShowLoginOrRegist(3)}>找回密码</Button>
                </Form.Item>
            </Form>
            <AxiosRequest
                responseDataHandle={responseDataHandle} api={loginApi.api} method={loginApi.method}
                reqObject={requestObj} requestCount={requestCount}
            ></AxiosRequest>
        </>
    )
}

CompanyLoginForm.defaultProps = {
    setShowLoginOrRegist: (showNumber) => console.log(showNumber),
    responseHandle: (resp) => console.log(resp),
}

export { CompanyLoginForm };
