// 腾讯云 cos 上传函数
const cosConfig = {
  Bucket: 'purchase-records-1254307677',
  Region: 'ap-chengdu',
  StorageClass: 'STANDARD',
};

const util = {
  createFile: function (options) {
      var buffer = new ArrayBuffer(options.size || 0);
      var arr = new Uint8Array(buffer);
      [].forEach.call(arr, function (char, i) {
          arr[i] = 0;
      });
      var opt = {};
      options.type && (opt.type = options.type);
      var blob = new Blob([buffer], options);
      return blob;
  }
};

// 对更多字符编码的 url encode 格式
const camSafeUrlEncode = function (str) {
  return encodeURIComponent(str)
      .replace(/!/g, '%21')
      .replace(/'/g, '%27')
      .replace(/\(/g, '%28')
      .replace(/\)/g, '%29')
      .replace(/\*/g, '%2A');
};

export { cosConfig, util, camSafeUrlEncode };
