import React, { useEffect, useState } from 'react';
import { Avatar, Menu, Dropdown } from 'antd';
import { UserOutlined, DownOutlined, ReloadOutlined, DownCircleFilled } from '@ant-design/icons';

import { CompanyInfoDrawer } from '../../CompanyInfo';
import { CompanyLoginDrawer } from '../../CompanyLogin';
import { getCompanyData } from '../../CompanyInfo/get_company_info';
import { removeToken } from '../../../apis/token_handle';
// import {
//     Button, Space, Affix, Layout, Row, Col, Avatar, Menu, Dropdown, Select, Form, Tooltip,
// } from 'antd';

const companyMenu = ( companyName, companyInfo, setCompanyInfo, visible, setVisible ) => {
    // user menu

    const logoutHandle = () => {
        removeToken();
        setCompanyInfo({});
    };

    const openCompanyInfoDrawer = () => {
        // console.log('~~~~~~~~~~ Close Company Info Drawer! ~~~~~~~~~~~~');
        setVisible(1);
    }

    const openCompanyLoginDrawer = () => {
        // console.log('~~~~~~~~~~ Close Company Info Drawer! ~~~~~~~~~~~~');
        setCompanyInfo({});
        setVisible(2);
    }

    return (<Menu>
        {
            companyInfo.id && Number(companyInfo.id) > 0
            ? <>
                <Menu.Item key="3" disabled>{companyName}</Menu.Item>
                <Menu.Divider />
                <Menu.Item key="c1" onClick={openCompanyInfoDrawer}>详情</Menu.Item>
                <Menu.Item key="c2" onClick={logoutHandle}>退出</Menu.Item>
            </>
            : <Menu.Item key="c3" onClick={openCompanyLoginDrawer}>登陆</Menu.Item>
        }
    </Menu>)
};

const CompanyComponent = () => {
    // 公司logo
    const [companyInfo, setCompanyInfo] = useState({});
    const [companyName, setCompanyName] = useState('请修改用户名');
    const [drawerVisible, setDrawerVisible] = useState(0);
    const [logoUrl, setLogoUrl] = useState('');

    useEffect(() => {
        if (companyInfo.id && Number(companyInfo.id) > 0) {
            setCompanyName(companyInfo.name && companyInfo.name.length > 0 ? companyInfo.name : '请修改用户名');
            setLogoUrl(companyInfo.avatar_url);
        } else {
            setCompanyName('请登陆');
            getCompanyData().then(resp => {
                setCompanyInfo(resp);
            }).catch(err => {
                console.log(err);
            })
        }
    }, [companyInfo]);

    return (
        <div>
            <Dropdown overlay={() => companyMenu(companyName, companyInfo, setCompanyInfo, drawerVisible, setDrawerVisible)}>
                <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                    <Avatar size="large" icon={<UserOutlined />} src={logoUrl ? logoUrl : ''} /> &nbsp; <DownCircleFilled />
                </a>
            </Dropdown>

            <CompanyInfoDrawer visible={drawerVisible === 1} setVisible={setDrawerVisible} companyInfo={companyInfo} setCompanyInfo={setCompanyInfo}></CompanyInfoDrawer>
            <CompanyLoginDrawer visible={drawerVisible === 2} setVisible={setDrawerVisible} companyInfo={companyInfo} setCompanyInfo={setCompanyInfo}></CompanyLoginDrawer>
        </div>
    );
};

export {
    CompanyComponent,
};
