import React, { useEffect, useState } from 'react';
import { labelsApi, } from '../../apis/server_apis';
import { sendAxiosRequest } from '../../apis/common_request';
import { Button, Space, Avatar, Form, message, Input } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { siteStyles } from '../../custom_configs';
import { ModalUploadImage } from '../modal_components/modal_upload_image';

const layout = siteStyles.formLayoutStyle;
const tailLayout = siteStyles.formTailLayoutStyle;
const avatarSize = { xs: 48, sm: 64, md: 96, lg: 96, xl: 128, xxl: 128 };

const getCompanyData = () => {
    return new Promise((resolve, reject) => {
        // 获取顾客自己到信息
        const companyInfoApiObj = labelsApi.companyInfo;
        sendAxiosRequest(companyInfoApiObj.api, companyInfoApiObj.method, {}, {}).then(res => {
            if (res.code && res.code === 1000) {
                resolve(res);
            } else {
                console.log(res);
                reject(res);
            }
            
        }).catch(err => {
            console.log(err);
            reject(err);
        });
    });
}

const updateCompanyData = (newCompanyDataObj) => {
    // 更新用户信息
    return new Promise((resolve, reject) => {
        // 更新
        const updateCompanyInfoApiObj = labelsApi.updateCompanyInfo;
        sendAxiosRequest(updateCompanyInfoApiObj.api, updateCompanyInfoApiObj.method, {}, newCompanyDataObj).then(res => {
            if (res.code && res.code === 1000) {
                resolve(res);
            } else {
                console.log(res);
                reject(res);
            }
        }).catch(err => {
            console.log(err);
            reject(err);
        });
    })
}

const UpdateCompanyNameComponent = ({companyInfo, setCompanyInfo, setUpdateNameState}) => {
    // 更新公司昵称组件
    
    const onFinish = (values) => {
        // 更新昵称
        console.log(values);
        updateCompanyData(values).then(res => {
            console.log(res);
            setCompanyInfo(res);
            setUpdateNameState(0)
        }).catch(err => {
            console.log(err);
            message.error('更新失败...');
        });
    }

    return (
        <Form
            {...layout}
            name="updateCompanyName"
            initialValues={{
                name: companyInfo.name ? companyInfo.name : '',
            }}
            onFinish={onFinish}
        >
            <Form.Item
                label="新昵称"
                name="name"
                rules={[{
                    required: true,
                    message: 'Please input your nickname!',
                }]}
            ><Input /></Form.Item>
            <Form.Item {...tailLayout}>
                <Space>
                    <Button type="primary" htmlType="submit">提交</Button>
                    <Button onClick={() => setUpdateNameState(0)}>取消</Button>
                </Space>
            </Form.Item>
        </Form>
    )
}


const CompanyInfoComponent = ({ companyInfo, setCompanyInfo }) => {
    // 公司详情组件
    // const [companyInfo, setCompanyInfo] = useState({});

    const [updateNameState, setUpdateNameState] = useState(0);

    const [modalVisible, setModalVisible] = useState(0);

    const updateCompanyInfo = () => {
        // 更新用户详情
        getCompanyData().then(res => {
            console.log(res);
            setCompanyInfo(res);
        }).catch(err => {
            console.log(err);
            setCompanyInfo({});
        })
    }

    useEffect(() => {
        updateCompanyInfo()
    }, []);

    const show_avatar_modal = () => {
        // 显示上传头像对话框
        setModalVisible(1);
    };

    const uploadedAvatarResponseHandle = (resp) => {
        // 上传头像后操作(更新头像 cos key)
        const avatarCosKey = resp.image_cos_name ? resp.image_cos_name : ''
        if (avatarCosKey.length > 5) {
            // 更新头像
            updateCompanyData({ avatar: avatarCosKey }).then(res => {
                console.log(res);
                updateCompanyInfo();
            }).catch(err => {
                console.error(err);
            });
        }
    }

    return (
        <div>
            <Space>
                {/* <p><b>用户详情</b></p> */}
                <Button onClick={updateCompanyInfo} type="link">刷新</Button>
            </Space>
            <hr></hr>
            <div style={{height: '100%', width: '100%', padding: '5px', textAlign: 'center'}}>
                <Space direction="vertical">
                    <Space>
                        <Avatar size={avatarSize} icon={<UserOutlined />} src={companyInfo.avatar_url}></Avatar>
                        <Button onClick={show_avatar_modal} type="link">上传Logo</Button>
                    </Space>
                    {
                        updateNameState === 1
                        ? <UpdateCompanyNameComponent
                            companyInfo={companyInfo}
                            setCompanyInfo={setCompanyInfo}
                            setUpdateNameState={setUpdateNameState}></UpdateCompanyNameComponent>
                        : <>
                            <p><b>{companyInfo.name ? companyInfo.name : '昵称'}</b></p>
                            <Button onClick={() => setUpdateNameState(1)} type="link">修改昵称</Button>
                        </>
                    }
                    
                </Space>
            </div>
            <ModalUploadImage showVisibleNum={1} visible={modalVisible} setVisible={setModalVisible} uploadedResponseHandle={uploadedAvatarResponseHandle}></ModalUploadImage>
        </div>
    )
}

export { getCompanyData, updateCompanyData, CompanyInfoComponent };
